.data-year-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    // margin-left: -4em !important;
    align-content: left;
    label {
        @include typeFilterPre;
    }
    .data-year {
        &:hover,&:focus,& {
            background-color: transparent;
        }
        &:hover {
            cursor: pointer;
            transition: all 220ms ease-out;
            color: $light;
            background-color: $brandBlue;
            background-image:
            linear-gradient(45deg, transparent 50%, $light 50%),
            linear-gradient(135deg, $light 50%, transparent 50%);
          }
        border: 1px solid $brandBlue;
        border-radius: $borderRadius;
        @include typeFilter;
        color: $brandBlue;
        
        display: flex;
        padding: 0.45rem 2rem 0.45rem 1.25rem;
        align-items: center;
        gap: 0.5rem;
        appearance: none;
        margin: 0 0 0 1em;

        background-image:
            linear-gradient(45deg, transparent 50%, $brandBlue 50%),
            linear-gradient(135deg, $brandBlue 50%, transparent 50%);

        background-position:
            calc(100% - 20px) calc(1em),
            calc(100% - 15px) calc(1em),
            calc(100% - 15px);

        background-position-y: center;
            
        background-size:
            5px 5px,
            5px 5px;

        background-repeat: no-repeat;
      }
}